    <div class="dialog-footer" style="text-align:center;margin-top:20px;">
      <template v-if="TableData.auditor_status == 2">
        <!-- 已审核 -->
        <el-button @click="audit('getauditorInvalid')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <!-- 未审核 -->
      <template v-if="TableData.auditor_status == 1">
        <el-button type="primary" @click="audit('getauidtorPass')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="audit('getauditorInvalid')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <!-- 作废 -->
      <template v-if="TableData.auditor_status == 3 || TableData.auditor_status == 2 || (authority.b_invalid != 1 && authority.b_pass != 1)">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "instorageOldtyresstorageTirebackstorageSee",
  data() {
    return {
      authority: {},
      TableData: {
        goods_info: [] //只有新增的时候需要
      }
    };
  },
  computed: {
    ...mapGetters(["tirebackstorageSeeGet"])
  },
  methods: {
    ...mapActions(["tirebackstorageIndexAct"]),
    goBack() {
      this.$router.push({
        path: "/inventory/tireback"
      });
      this.tirebackstorageIndexAct({
        goPage: this.tirebackstorageSeeGet.goPage,
        params: this.tirebackstorageSeeGet.params,
        pageSize: this.tirebackstorageSeeGet.pageSize
      });
    },
    audit(api) {
      //审核
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 15,
          bill_id: this.TableData.outstock_id,
          operation_type: api === "getauditorInvalid" ? 6 : 4
        })
        .then(() => {
          this.$axMethod[api]({
            outstock_id: this.TableData.outstock_id
          }).then(({ data }) => {
            if (data.code == 200) {
              this.$message({
                message: data.msg,
                type: "success"
              });
              this.goBack();
            }
          });
        });
    },
    initData() {
      //详情数据
      let outstock_id = "";
      outstock_id = this.tirebackstorageSeeGet.outstock_id;
      this.$axMethod
        .getcheckOutstock({
          outstock_id: outstock_id
        })
        .then(({ data }) => {
          if (data.code == "200") {
            this.$set(this, "TableData", data.data);
          }
        });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "总计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if ([5, 7].includes(index)) {
            sums[index] = parseFloat(sums[index]);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    }
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.TableData.outstock_id = this.tirebackstorageSeeGet.outstock_id;
    if (this.tirebackstorageSeeGet.outstock_id) this.initData();
  }
};
</script>

